import React from 'react';
import { graphql, navigate } from 'gatsby';
import Img from 'gatsby-image';
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';

// Components
import Layout from '../Layout/Layout';

export const query = graphql`
	query($slug: String!) {
		contentfulBlog(slug: { eq: $slug }) {
			title
			desc {
				json
			}
			img {
				fluid {
					...GatsbyContentfulFluid
				}
			}
		}
	}
`;

function Pest({ data }) {
	const options = {
		renderNode: {
			'embedded-asset-block': (node) => {
				const url = node.data.target.fields.file['en-US'].url;
				return (
					<div className="d-flex justify-content-center">
						<img className="img-fluid" src={url} alt="img" />
					</div>
				);
			}
		}
	};

	return (
		<Layout title={`${data.contentfulBlog.title}`}>
			<div className="container mt-5">
				<h2 className="text-right mr-5">{data.contentfulBlog.title}</h2>
				<div className="d-flex justify-content-end">
					<Img style={{ width: '300px' }} fluid={data.contentfulBlog.img.fluid} />
				</div>
				<div className="text-right mt-4" dir="rtl">
					{documentToReactComponents(data.contentfulBlog.desc.json, options)}
				</div>
				<div className="d-flex justify-content-end p-4">
					<button
						style={{ background: 'none', border: 'none', color: 'grey', outline: 'none' }}
						onClick={() => navigate(-1)}
					>
						بازگشت به صفحه قبل
					</button>
				</div>
			</div>
		</Layout>
	);
}

export default Pest;
